.search-panel {
  display: flex;
  margin-bottom: 1rem;
  min-height: 2rem;
  padding: 5px;
}

.search-panel__input {
  margin-right: 0.3rem;
  border-radius: 5px;
  border: 2px solid lightgray;
}

.search-panel__input:focus {
  outline: none;
}

.search-panel__actions {
  flex-grow: 2;
  display: flex;
}

.search-panel__actions * {
  flex-grow: 2;
  background-color: transparent;
  border-radius: 5px;
  border: 2px gray solid;
}

.search-panel__actions button:hover {
  cursor: pointer;
  background-color: dodgerblue;
}

.active {
  background-color: dodgerblue;
  color: white;
  border-color: dodgerblue;
}
