.todo-list .todo-list-item {
  display: flex;
  width: 100%;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
}

.todo-list-item:hover {
  cursor: pointer;
}

.todo-list-item .delete {
  color: green;
  border-color: green;
  margin-left: auto;
}

.todo-list-item.important {
  color: dodgerblue;
}

.todo-list-item .important-btn {
  color: dodgerblue;
  border-color: dodgerblue;
  margin-left: 0.5rem;
}

.todo-list-item button {
  background-color: transparent;
  border: 2px solid gray;
  border-radius: 5px;
  min-width: 2rem;
  min-height: 2rem;
}

.todo-list-item .delete:hover {
  box-shadow: 0 0 5px green;
}

.todo-list-item .important-btn:hover {
  box-shadow: 0 0 5px dodgerblue;
}

.todo-list-item span {
  font-weight: 600;
  font-size: 1.3rem;
}

.todo-list-item .done {
  text-decoration: line-through;
}
